import React from 'react';
import App from './App';

import helloVR1 from './img/helloVR_1.jpeg';
import diagram from './img/block_diagram.png'
import environment1 from './img/environment1a.png';
import environment2 from './img/environment1b.png';
import environment3 from './img/environment2.png';
import model1 from './img/model1.png';
import model2 from './img/model2.png';
import mount1 from './img/mount1.jpg';
import mount2 from './img/mount2.jpg';
import mount3 from './img/mount3.jpg';
import smile from './img/smile.png';
import head from './img/head.png';
import body from './img/body.png';
import arm from './img/arm.png';
import finger from './img/finger.png';
import poster from './img/helloVR_poster.jpg';

function Portfolio() {
    return (
        <App>
            <div className="Content">
                <div className="Content-portfolio">
                    <h1>HelloVR</h1>
                    <p>Virtual Reality Chat Solution with Real-Time Face and Hand Tracking</p>
                    <div>
                        <ul>
                        <li>Project Duration: 2016 May - 2017 March</li>
                        <li>Participants: 4</li>
                        <li>Symposium Date: 2017 March 22</li>
                        <li>Symposium Location: University of Waterloo Davis Center</li>
                        </ul>
                        <h2>Abstract</h2>
                        <p>The objective of HelloVR is to provide an immersive virtual communication platform 
                            to help create a more personal connection between people. </p>
                        <p>Using the data from various input devices such as finger, 
                            head, facial and body movement tracking, and applying this to virtual humanoid avatars, a person’s subtle movements
                            can be recreated and observed by the other party through the use of a VR headset. </p>
                        <p>A game engine is leveraged for it’s built in rendering, simulation, and networking capabilities, while the processing 
                            of inputs such as facial movements is accomplished by using a plugin to the game engine. </p>
                        <img src={helloVR1} alt="helloVR_Symposium_picture"/>
                        <h2>Motivation</h2>
                        <p>Despite advances in video chat technology, existing video communication solutions still lack key aspects of talking to 
                            someone face-to-face, such as the ability to feel like you are inside a conversation rather than simply talking to a screen.</p>
                        <h2>Block Diagram</h2>
                        <img src={diagram} alt="helloVR_block_diagram"/>
                        <p>Six subsystems are considered for the design.</p>
                        <h2>Specifications</h2>
                        <p>Project specifications include:</p>
                        <ul>
                        <li>3D character models must be displayed in a virtual space</li>
                        <li>The character model's facial expressions, head/arm/body posisions and finger movements must mimic that of the controlling human</li>
                        <li>Audio captured by the controlling user's microphone should be played back to other users</li>
                        <li>At least two VR environments are provided for the character models</li>
                        <li>There exists at least one virtual item that can be picked up or moved</li>
                        </ul>
                        <h2>Virtual Environments</h2>
                        <div className="image-row">
                            <div className="image-column3"><img src={environment1} alt="environment"/></div>
                            <div className="image-column3"><img src={environment2} alt="environment"/></div>
                            <div className="image-column3"><img src={environment3} alt="environment"/></div>
                        </div>
                        <h2>Character Models</h2>
                        <div className="image-row">
                            <div className="image-column2"><img src={model1} alt="model"/></div>
                            <div className="image-column2"><img src={model2} alt="model"/></div>
                        </div>
                        <h2>Face Tracking</h2>
                        <p>We considered several options for tracking facial expressions including using Kinect.</p>
                        <figure>
                        <img src="https://www.codeproject.com/KB/library/394975/FaceMask.jpg" alt="https://www.codeproject.com/KB/library/394975/FaceMask.jpg"/>
                        <footer><small>Image from CodeProject, 1999-{(new Date().getFullYear())} https://www.codeproject.com/KB/library/394975/FaceMask.jpg</small></footer>
                        </figure>
                        <p>However, with VR headset blocking the upper half of user's face, Kinect won't be able to track faces.</p>
                        <p>We ended up having a camera mounted on VR headset.</p>
                        <div className="image-row">
                            <div className="image-column3"><img src={mount1} alt="headset_with_mounted_camera"/></div>
                            <div className="image-column3"><img src={mount2} alt="headset_with_mounted_camera"/></div>
                            <div className="image-column3"><img src={mount3} alt="headset_with_mounted_camera"/></div>
                        </div>
                        <p>We used OpenCV to train for smile detection in collaboration with UE4 Blueprint in order to mirror user smiles on virtual avatars.</p>
                        <img src={smile} alt="smile_blueprint"/>
                        <h2>Head/Body/Arm Tracking</h2>
                        <p>We used HTC Vive positional data and Kinect for tracking user's head, body and arm movements.</p>
                        <div className="image-row">
                            <div className="image-column3"><img src={head} alt="head_tracking"/></div>
                            <div className="image-column3"><img src={body} alt="body_tracking"/></div>
                            <div className="image-column3"><img src={arm} alt="arm_tracking"/></div>
                        </div>
                        <h2>Finger Tracking</h2>
                        <p>Leap Motion was mounted on HTC Vive headset for finger tracking. We used the same set up as below.</p>
                        <figure>
                        <img src="https://cdn.slashgear.com/wp-content/uploads/2016/04/iveleap.jpg" alt="leap_motion_mount_on_htcvive"/>
                        <footer><small>Image from 2005-{(new Date().getFullYear())} SlashGear https://cdn.slashgear.com/wp-content/uploads/2016/04/iveleap.jpg</small></footer>
                        </figure>
                        <img src={finger} alt="finger_tracking"/>
                        <h2>Symposium Poster</h2>
                        <img src={poster} alt="poster"/>
                    </div>
                </div>
            </div>
        </App>
    );
};

export default Portfolio;